import { Link } from 'react-router-dom';

import './artistCard.scss'

const ArtistCard = ({id,name, charname, imgLink}) => {
    return(
            <Link to={`actor/${id}`} className="artist-card">
                <img src={imgLink} alt=""/>
                <h2 className="actor-name">{name}</h2>
                <h3>{charname}</h3>
            </Link>
    )
}

export default ArtistCard;