import './adviseActor.scss'

import { useEffect, useState } from 'react';
import axios from 'axios';

import AdviseActorCard from '../../components/adciseActorCard/adviseActorCard'

const AdviseActor = ({ match }) => {

    const [actorMovie, setActorMovie] = useState ({
        isFetched: false,
        data: [],
        error: null
    })

    useEffect(() => {
        axios.get(`https://api.themoviedb.org/3/person/${match.params.id}/movie_credits`, {
            params: {
                api_key: '8d08d31e1b08de961a19e2ae293de867'
            }
          })
          .then(function (response) {
            setActorMovie({
                isFetched: true,
                data: response.data,
                error: false

            })
          })
          .catch(function (error) {
            setActorMovie({
                isFetched: true,
                data: [],
                error: error

            })
          })
          .then(function () {
              
          });  
    }, [])

    console.log(actorMovie);

    return (
        <div className="advise_actor container">
            <div className="advise_actor-list">
                    {
                actorMovie.isFetched ? (
                    <div className="advise">
                {
                    actorMovie.data.cast.map((actor, index) => (
                        <AdviseActorCard
                            key={index}
                            id={actor.id}
                            name={actor.title}
                            imgLink={`https://image.tmdb.org/t/p/w500/${actor.backdrop_path}`}
                        />
                    ))
                }
            </div>
                    ) : (
                    <h1>Loading...</h1>
                )
                }
            </div>
        </div>
    )
}

export default AdviseActor;