import './pages.scss'

import { useEffect, useState } from 'react';
import axios from 'axios';

import ArtistCard from '../components/artistcard/artistCard'
import Advise from '../containers/advise/advise'

const SingleMoviePage = ({ match }) => {

    const [movieInfo, setmovieInfo] = useState ({
        isFetched: false,
        data: [],
        error: null
    })

    const [actorList, setActorInfo] = useState ({
        isCome: false,
        data: [],
        error: null
    })

    const [adviseMovies, setAdviseMovies] = useState ({
        isCome: false,
        data: [],
        error: null
    })

    // console.log(actorList)

    useEffect(() => {
        axios.get(`https://api.themoviedb.org/3/movie/${match.params.id}`, {
            params: {
                api_key: '8d08d31e1b08de961a19e2ae293de867'
            }
          })
          .then(function (response) {
            setmovieInfo({
                isFetched: true,
                data: response.data,
                error: false

            })
          })
          .catch(function (error) {
            setmovieInfo({
                isFetched: true,
                data: [],
                error: error

            })
          })
          .then(function () {
              
          });  
    }, [])

    useEffect(() => {
        axios.get(`https://api.themoviedb.org/3/movie/${match.params.id}/recommendations`, {
            params: {
                api_key: '8d08d31e1b08de961a19e2ae293de867'
            }
          })
          .then(function (response) {
            setAdviseMovies({
                isFetched: true,
                data: response.data.results,
                error: false

            })
          })
          .catch(function (error) {
            setAdviseMovies({
                isFetched: true,
                data: [],
                error: error

            })
          })
          .then(function () {
              
          });  
    }, [])

    console.log(adviseMovies.data)

    useEffect(() => {
        axios.get(`https://api.themoviedb.org/3/movie/${match.params.id}/credits`, {
            params: {
                api_key: '8d08d31e1b08de961a19e2ae293de867'
            }
          })
          .then(function (response) {
            setActorInfo({
                isCome: true,
                data: response.data,
                error: false

            })
          })
          .catch(function (error) {
            setActorInfo({
                isCome: true,
                data: [],
                error: error

            })
          })
          .then(function () {
              
          });  
    }, [])

    // console.log(actorList.data)
    // console.log(actorList.data.crew)
    return (

        <div className="single-movie container"> 
        
            {
                movieInfo.isFetched ? (
                    <div className="movie-info"> 
                        <div className="info-left">
                        <img src={`https://image.tmdb.org/t/p/w500/${movieInfo.data.backdrop_path}`} alt=""/>   
                        </div>
                        <div className="info-right">
                        <img className="movie-poster" src={`https://image.tmdb.org/t/p/w500/${movieInfo.data.poster_path}`} alt=""/>
                        <div className="front-info">
                            <h3 className="movie-title"> <span className="main-title">Movie Name:</span> {movieInfo.data.title}</h3>
                            <h4 className="tagline"><span className="main-title">Tagline:</span> {movieInfo.data.tagline}</h4>
                            <p className="movie-overview"><span className="main-title">Overview:</span> {movieInfo.data.overview}</p>
                            <h4 className="movie-budget"> <span className="main-title">Budget:</span> ${movieInfo.data.budget}</h4>
                            <h4 className="runtime"> <span className="main-title">Run-time:</span> {movieInfo.data.runtime} minut</h4>
                            <h4 className="release"> <span className="main-title">Release date:</span> {movieInfo.data.release_date}</h4>
                            <a href={movieInfo.data.homepage} target="_blank" className="offi-page">Official Site: <button>Click</button></a>
                            
                            <div className="genre">
                                <h4>Genre: </h4>

                            {
                                
                                movieInfo.data.genres.map((genre, index) => (
                                        <button key={index}>{genre.name}</button>
                                ))
                                }
                            </div>

                            <h2 className="actor-title">Actors</h2>

                    <div className="actors-list">
                    {
                actorList.isCome ? (
                    <div className="actors">
                {
                    actorList.data.cast.map((actor, index) => (
                        <ArtistCard
                            key={index}
                            id={actor.id}
                            name={actor.name}
                            charname={actor.character}
                            imgLink={`https://image.tmdb.org/t/p/w500/${actor.profile_path}`}
                        />
                    ))
                }
            </div>
        ) : (
            <h1>Loading...</h1>
        )
    }
            </div>

                        </div>
                        </div>
                        
                    </div>
                ) : (
                    <h1>Loading....</h1>
                )
            }

                    <h1 className="advise-title">Recommendations</h1>

                        <div className="advise-block">
                            
                        {
                adviseMovies.isCome ? (
                    <div className="advise-movies">
                {
                    <h1>load</h1>
                }
            </div>
        ) : (
            adviseMovies.data.map((movie, index) => (
                <Advise
                    key={index}
                    id={movie.id}
                    name={movie.title}
                    imgLink={`https://image.tmdb.org/t/p/w500/${movie.backdrop_path}`}
                />
            ))
        )
    }
                        </div>

        </div>

        

        // <div className="single-movie-page">
        //     <h2 className="movie-name">{movieName}</h2>
        //     <h3 className="movie-overview">{movieOver}</h3>
        //     <img src={`https://image.tmdb.org/t/p/w500/${imgLink}`} alt="" className="movie-img"/>
        // </div>
    )
}

export default SingleMoviePage;