import { useEffect, useState } from 'react';
import axios from 'axios';
import MovieCard from '../components/movieCard/movieCard'

const SearchResults = ({ match }) => {

    const [moviesList, setMoviesList] = useState ({
        isFetched: false,
        data: [],
        error: null
    })

    useEffect(() => {
        axios.get(`https://api.themoviedb.org/3/search/movie?query=${match.params.searchQuery}`, {
            params: {
              api_key: '8d08d31e1b08de961a19e2ae293de867',
            },
          }).then(function (response) {
            console.log(response);
            setMoviesList({
              isFetched: true,
              data: response.data.results,
              error: false,
            });
          }).catch(function (error) {
            setMoviesList({
              isFetched: true,
              data: [],
              error: error,
            });
          }).then(function () {});
    }, [match.params.searchQuery]) 

    return (
        <div className="popular-movies container">
            {
                moviesList.isFetched ? (
                    <div className="info">
                        
                        {
                            moviesList.data.length > 0 ? (
                                    moviesList.data.map((item, index) => (
                                        <MovieCard
                                        id={item.id}
                                        imgLink={item.poster_path}
                                        title={item.title}
                                        key={index}
                                        rating={item.vote_average}
                                        releaseDate={item.release_date}
                                        />
                                    ))
                            ) : (
                                <h1>Unaqa kino yo karochchi</h1>
                            )
                        }

                    </div>
                ) : (
                    <h2>Loading...</h2>
                )
            }
        </div>
    )
}

export default SearchResults; 