import '../movieCard/movieCard.scss'

import { Link } from 'react-router-dom';

const HomeCard = ({imgLink, title, id,rating,releaseDate}) => {
    return (
        <Link to={`/movie/${id}`}>
            <div className="movie-card">
                
                <div className="card-rating">
                    {rating}
                </div>
                    

            <div className="movie-card-top">
                <img src={`https://image.tmdb.org/t/p/w500/${imgLink}`} className="movie-card-top__img" alt=""/>
            </div>
            <div className="movie-card-content">
                <h2 className="movie-card-content__title"> {title} </h2>

                <div className="release-date">
                   Release date: {releaseDate}
                </div>

                <Link to={`/movie/${id}`} className="movie-card-content__link">
                        More Info
                </Link>
            </div>
        </div>
        </Link>
    )
}

export default HomeCard;