import './footer.scss'
import { Link } from 'react-router-dom'

import Logo from '../../assets/images/T1.png'
import Facebook from '../../assets/icons/facebook.svg'
import Telegram from '../../assets/icons/telegram.svg'
import Twitter from '../../assets/icons/twitter.svg'

const Footer = () => {
    return (
        <div className="footer">
            <div className="footer-inner container">
                <div className="footer-left">
                    <img src={Logo} className="logo" alt=""/>

                    <h2 className="footer-info">© 2020 Alladdin</h2>
                </div>

                <div className="footer-center">
                    <Link className="header__link" to="/">
                            Top Rated Movies
                    </Link>
                    <Link className="header__link" to="/popular">
                            Popular Movies
                    </Link>
                    <Link className="header__link" to="/movies">
                            Upcoming Movies
                    </Link>
                    <Link className="header__link" to="/tv-show">
                            Tv Shows
                    </Link>
                </div>

                <div className="footer-right">
                    <div className="footer-right-top">
                        <img src={Facebook} alt=""/>
                        <img src={Telegram} alt=""/>
                        <img src={Twitter} alt=""/>
                    </div>

                    <div className="footer-right-bottom">
                        <h2 className="footer-right-bottom_info">
                         © 2020-2021.Nizamkhujaev Saidabror
                        </h2>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Footer;