import { Link } from 'react-router-dom';

import './adviseCard.scss'

const adviseActorCard = ({id,name, imgLink}) => {
    return(
            <Link to={`actor/${id}`} className="advise-card">
                <img src={imgLink} alt=""/>
                <h2 className="advise-actor-name">{name}</h2>
            </Link>
    )
}

export default adviseActorCard;