import { Link } from 'react-router-dom'
 
import './advise.scss'

const Advise = ({id,name, imgLink}) => {

    return (
        <div className="advise">

            <Link to={`actor/${id}`} className="advise-card">
        
            <img src={imgLink} alt=""/>
            <h2 className="movie-name">{name}</h2>
        </Link>

        </div>
    )
    
}

export default Advise;