import { useEffect, useState, } from 'react';
import axios from 'axios';

import './header.scss';
import Logo from '../../assets/images/T1.png'

import { Link } from 'react-router-dom'
import { useHistory } from 'react-router-dom'
 
const Header = () => {

    let history = useHistory();

    // const [moviesList, setMoviesList] = useState ({
    //     isFetched: false,
    //     data: [],
    //     error: null
    // })

    const [searchValue, setSearchValue] = useState('');

    useEffect(() => {
        if(searchValue.length > 0) {
            history.push(`/search/${searchValue}`)
        } else{
            history.push(`/`)
        }
      }, [searchValue]);

    return (
        <div className="header">
            <div className="header-inner container">
                <div className="header-left">
                    <img src={Logo} className="logo" alt=""/>
                </div>

                <div className="header-center">
                    <Link className="header__link" to="/">
                        Top Rated Movies
                    </Link>
                    <Link className="header__link" to="/popular">
                        Popular Movies
                    </Link>
                    <Link className="header__link" to="/movies">
                        Upcoming Movies
                    </Link>
                    <Link className="header__link" to="/tv-show">
                        Tv Shows
                    </Link>
                </div>

                <div className="header-right">
                    <input type="search" placeholder="Search" className="input" onChange={(e) => setSearchValue(e.target.value)} value={searchValue}/>
                </div>
            </div>
        </div>
    )
}

export default Header;